import { FavoriteBorderOutlined, Search } from "@material-ui/icons";
import React from "react";
import styled from "styled-components";
import { mobile } from "../responsive";

const Container = styled.div`
  height: 380px;
  width: 400px;
  background: rgb(134, 132, 172);
  background: -moz-linear-gradient(
    90deg,
    rgba(134, 132, 172, 1) 64%,
    rgba(174, 178, 169, 1) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(134, 132, 172, 1) 64%,
    rgba(174, 178, 169, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(134, 132, 172, 1) 64%,
    rgba(174, 178, 169, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#8684ac",endColorstr="#aeb2a9",GradientType=1);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  flex: 1;
`;

const Circle = styled.div`
  width: 280px;
  height: 280px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  z-index: 1;
`;

const Image = styled.img`
  height: 50%;
  margin: 100px;
  z-index: 2;

  ${mobile({ height: "150px" })}
`;

const Info = styled.div`
  height: 100%;
  width: 100%;
  top: 0;
  z-index: 3;

  left: 0;
  position: absolute;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
`;

const Icon = styled.div`
  width: 30px;
  border-radius: 50px;
  height: 30px;
  margin: 10px;
  background-color: #7f8c8d;
  color: white;
  background: rgb(134, 132, 172);
  background: -moz-linear-gradient(
    90deg,
    rgba(134, 132, 172, 1) 64%,
    rgba(174, 178, 169, 1) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(134, 132, 172, 1) 64%,
    rgba(174, 178, 169, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(134, 132, 172, 1) 64%,
    rgba(174, 178, 169, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#8684ac",endColorstr="#aeb2a9",GradientType=1);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease;

  &:hover {
    transform: scale(1.2);
  }
`;

const Products = ({ item }) => {
  return (
    <Container>
      <Circle />
      <Image src={item.img} />
      <Info>
        <Icon>
          <Search />
        </Icon>
        <Icon>
          <FavoriteBorderOutlined />
        </Icon>
      </Info>
    </Container>
  );
};

export default Products;
